import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../../components/panel/panel.jsx";
import { connect } from "react-redux";
import { getDataParams2 } from "../../../../config/axios.jsx";
import { reduxForm } from "redux-form";
import {
  formatDate,
  localStorageencryp,
} from "../../../../components/helpers/function.jsx";
import {
  NotifError,
  ToastNotification,
} from "../../../../components/helpers/notification.jsx";
import * as moment from "moment-timezone";
import HeadKirimBarang from "./HeadKirimBarang";
import { getDataBaki } from "../../../../actions/datamaster_action.jsx";

const maptostate = (state) => {
  return {
    hideModal: state.datamaster.modalDialog,
    dataBaki: state.datamaster.getDataBaki,
  };
};

class FormLaporanKirimBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      username: false,
      export: false,
      LaporanDataBarang: [],
      tgl_awal: new Date(),
      tgl_akhir: new Date(),
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    if (typeof data.tujuan === "undefined") {
      return ToastNotification("warning", "Mohon pilih tujuan");
    }
    this.setState({
      isLoading: true,
    });

    let data_post = {
      tgl_from: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_to: formatDate(moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")),
      kode_toko: data.tujuan,
    };
    getDataParams2("kirim-barang/reports", data_post)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
          this.setState({
            isLoading: false,
            export: false,
            LaporanDataBarang: [],
          });
        } else {
          var result = [];
          const array = res.data;
          const listBaki = this.props.dataBaki;
          array.reduce(function (res, value) {
            if (!res[value.kode_toko]) {
              res[value.kode_toko] = {
                kode_toko: value.kode_toko,
                jumlah_berat: 0,
                jumlah_pcs: 0,
                no_kirim: value.no_kirim,
                nama_baki: listBaki.find((a) => a.kode_baki === value.kode_toko)
                  .nama_baki,
              };
              result.push(res[value.kode_toko]);
            }
            res[value.kode_toko].jumlah_berat += value.berat;
            res[value.kode_toko].jumlah_pcs += 1;
            return res;
          }, {});
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          ToastNotification("success", "Laporan Kirim Barang Tersedia");
          this.setState({
            LaporanDataBarang: res.data,
            LaporanDataBarangRekap: result,
            isLoading: false,
            export: true,
          });
        }
      })
      .catch((error) => this.errorHandling(error));
  }

  errorHandling(err) {
    NotifError(err);
    this.setState({
      isLoading: false,
      export: false,
    });
  }

  componentDidMount() {
    this.props.dispatch(getDataBaki());
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan Barang</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Kirim Barang</li>
        </ol>
        <h1 className="page-header">Laporan Kirim Barang </h1>
        <Panel>
          <PanelHeader>Filter Laporan</PanelHeader>
          <br />
          <div className="container">
            <HeadKirimBarang
              export={this.state.export}
              LaporanDataBarang={this.state.LaporanDataBarang}
              LaporanDataBarangRekap={this.state.LaporanDataBarangRekap}
              isLoading={this.state.isLoading}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </div>
          <br />
        </Panel>
      </div>
    );
  }
}

FormLaporanKirimBarang = reduxForm({
  form: "FormLaporanKirimBarang",
  enableReinitialize: true,
})(FormLaporanKirimBarang);
export default connect(maptostate, null)(FormLaporanKirimBarang);
