import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";

import {
  showModal,
  getParameterPotongan,
  ShowModalParameterPotongan,
  getMasterToko,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import {
  NotifError,
  NotifSucces,
  ToastNotification,
} from "../../../components/helpers/notification.jsx";
import {
  deleteData,
  postDataNoTImeOut,
  postParameterHarga,
  putDataNoTImeOut,
  putParameterHarga,
} from "../../../config/axios.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import HeadPotonganKategori from "./HeadPotonganKategori.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";
import Swal from "sweetalert2";

const maptostate = (state) => {
  return {
    getParameterPotongan: state.datamaster.getParameterPotongan,
    getMasterToko: state.datamaster.getMasterToko,
    hideModal: state.datamaster.modalDialog,
  };
};

const hapus = (params, dispatch, listToko) => {
  Swal.fire({
    // title: "Anda Yakin !!",
    // text: "Ingin Menghapus Data Ini ?",
    html: "Apakah Anda Yakin Ingin Data Ini ?",
    icon: "warning",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  })
    .then(async (result) => {
      if (result.isConfirmed) {
        try {
          for (const data of listToko) {
            await putParameterHarga(
              data.portal + "/api/v1/parameter-potongan/delete",
              { kode_group: params }
            );
          }
          NotifSucces("Data Berhasil Di Hapus");
        } catch (error) {
          NotifError("Data Gagal Di Hapus");
        }
        deleteData("parameter-potongan/" + params).then(() => {
          NotifSucces("Data Berhasil Di Hapus").then(() =>
            dispatch(getParameterPotongan())
          );
        });
      }
    })
    .catch((err) => {
      ToastNotification("error", err.response.data);
    });
};

class FormPotonganKategori extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      columns: [
        {
          dataField: "kode_group",
          text: "Kode Group",
          sort: true,
        },
        {
          dataField: "potongan_markis",
          text: "Potongan Markis",
          formatter: (cell) => {
            return <span>{cell} %</span>;
          },
        },
        {
          dataField: "potongan_harga",
          text: "Potongan Harga",
          formatter: (cell) => {
            return <span>{parseFloat(cell).toLocaleString("kr-KO")}</span>;
          },
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEditParameterPotonganKategori(row)}
                    className="btn btn-primary mr-3"
                  >
                    {" "}
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() => {
                      hapus(
                        row.kode_group,
                        this.props.dispatch,
                        this.props.getMasterToko
                      );
                    }}
                    className="btn btn-danger mr-3"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.dispatch(getParameterPotongan());
    this.props.dispatch(getMasterToko());
  }
  async handleSubmit(data) {
    this.setState({
      isLoading: true,
    });

    let data_post = {
      kode_group: data.kode_group,
      potongan_harga: data.harga,
      potongan_markis: data.markis,
    };

    let listToko = this.props.getMasterToko;
    if (this.state.isEdit) {
      try {
        for (const data of listToko) {
          await putParameterHarga(
            data.portal + "/api/v1/parameter-potongan",
            data_post
          );
        }
        ToastNotification("success", "Berhasil Tambah Parameter");
      } catch (error) {
        ToastNotification("error", "Gagal Tambah Parameter");
      }
      putDataNoTImeOut("parameter-potongan/" + data._id, data_post).catch((e) =>
        ToastNotification("error", "Gagal Tambah Parameter")
      );
    } else {
      try {
        for (const data of listToko) {
          await postParameterHarga(
            data.portal + "/api/v1/parameter-potongan/",
            data_post
          );
        }
        ToastNotification("success", "Berhasil Tambah Parameter");
      } catch (error) {
        ToastNotification("error", "Gagal Tambah Parameter");
      }
      postDataNoTImeOut("parameter-potongan/", data_post).catch((e) =>
        ToastNotification("error", "Gagal Tambah Parameter")
      );
    }
  }
  formEditParameterPotonganKategori(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalParameterPotongan(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  formTambahParameterPotonganKategori(dataEdit) {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: false,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">
            Parameter Potongan Kategori
          </li>
        </ol>
        <h1 className="page-header">Parameter Potongan Kategori</h1>
        <Panel>
          <PanelHeader>Parameter Potongan Kategori</PanelHeader>
          <br />

          {/* Parameter Pint */}
          <div className="row col-lg-12 justify-content-end">
            <div className="col-lg-2">
              <button
                className="btn btn-primary btn-block"
                onClick={() => this.formTambahParameterPotonganKategori()}
              >
                Tambah Data
              </button>
            </div>
          </div>
          <div className="container">
            <Tabel
              keyField="kode_group"
              exportCsv={true}
              data={this.props.getParameterPotongan}
              columns={this.state.columns}
              empty={this.props.ParameterHargaEmas}
              textEmpty="Parameter Harga Emas Kosong"
            />
          </div>
          <br />
          {/* End Master Sales */}
          <ModalGlobal
            size="P"
            title={
              this.state.isEdit
                ? "Edit Parameter Potongan Kategori"
                : "Tambah Parameter Potongan Kategori"
            }
            content={
              <HeadPotonganKategori
                isLoading={this.state.isLoading}
                isEdit={this.state.isEdit}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* <Modal  title={ this.state.isEdit ? 'Edit Parameter HargaEmas' : 'Tambah Parameter HargaEmas' } content={ <FormModalKategori isEdit={this.state.isEdit} /> } isOpen={ this.props.hideModal} onSubmit={ (data)=>this.handleSubmit(data) } /> */}

          {/* End Tambah Master Sales  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(FormPotonganKategori);
