import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { GetDataGroup, hideModal } from "../../../actions/datamaster_action";
import { ReanderField, ReanderSelect } from "../../../components/helpers/field";
// import ValidasiMasterSales from "../../../Validasi/ValidasiMasterSales.jsx";
import { createNumberMask } from "redux-form-input-masks";
import { Gramasi } from "../../../Validasi/normalize";
// import Loading from 'react-fullscreen-loading';

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "kr-KO",
});
const maptostate = (state) => {
  if (state.datamaster.editParameterPotongan !== null) {
    //   console.log(state.datamaster.ShowModalParameterHargaEmas);
    return {
      datagroup: state.datamaster.GetDataGroup,
      initialValues: {
        kode_group: state.datamaster.editParameterPotongan.kode_group,
        harga: state.datamaster.editParameterPotongan.potongan_harga,
        markis: state.datamaster.editParameterPotongan.potongan_markis,
        _id: state.datamaster.editParameterPotongan._id,
      },
    };
  }
};

class HeadPotonganKategori extends Component {
  state = {
    aktif: false,
  };
  componentDidMount() {
    this.props.dispatch(GetDataGroup());
    setTimeout(() => {
      document.getElementById("harga").focus();
    }, 100);
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        autoComplete="off"
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
      >
        <Field
          label="Kode Group"
          name="kode_group"
          options={this.props.datagroup
            .filter((list) => list.kode_group !== undefined)
            .map((list) => {
              let data = {
                value: list.kode_group,
                name: list.kode_group + " - " + list.nama_group,
              };
              return data;
            })}
          value={this.props.kode_group}
          disabled={this.props.isEdit}
          placeholder={"Silahkan Pilih Kode Group"}
          component={ReanderSelect}
        />
        <Field
          id="harga"
          name="harga"
          tabIndex="1"
          {...currencyMask}
          component={ReanderField}
          type="text"
          label="Jumlah Harga"
          onBlur={() => this.setState({ aktif: true })}
          placeholder="Masukan Jumlah Harga"
        />
        <Field
          id="markis"
          name="markis"
          tabIndex="2"
          component={ReanderField}
          type="text"
          normalize={Gramasi}
          label="Markis %"
          onBlur={() => this.setState({ aktif: true })}
          placeholder="Masukan Jumlah Markis"
        />
        <div className="col-lg-12 d-none">
          <Field
            id="_id"
            name="_id"
            tabIndex="1"
            {...currencyMask}
            component={ReanderField}
            type="text"
            label="Jumlah Harga"
            onBlur={() => this.setState({ aktif: true })}
            placeholder="Masukan Jumlah Harga"
          />
        </div>
        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
            >
              Batal
            </button>
            &nbsp;
            <button
              tabIndex="2"
              className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
        {/* {/* {this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null} */}
      </form>
    );
  }
}

HeadPotonganKategori = reduxForm({
  form: "HeadPotonganKategori",
  enableReinitialize: true,
  //   validate: ValidasiMasterSales,
})(HeadPotonganKategori);
export default connect(maptostate, null)(HeadPotonganKategori);
